/* Dashboard Components Styles */
.stat-card {
  background: var(--white-color);
  border-radius: var(--border-radius-lg);
  padding: 1.5rem;
  box-shadow: var(--shadow);
  transition: all 0.3s ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.stat-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--accent-color);
}

.stat-primary::before { background: var(--accent-color); }
.stat-success::before { background: #10b981; }
.stat-warning::before { background: #f59e0b; }
.stat-danger::before { background: #ef4444; }
.stat-info::before { background: #3b82f6; }
.stat-purple::before { background: #8b5cf6; }

.stat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.stat-icon {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: var(--white-color);
}

.stat-primary .stat-icon { background: var(--accent-color); }
.stat-success .stat-icon { background: #10b981; }
.stat-warning .stat-icon { background: #f59e0b; }
.stat-danger .stat-icon { background: #ef4444; }
.stat-info .stat-icon { background: #3b82f6; }
.stat-purple .stat-icon { background: #8b5cf6; }

.stat-trend {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.trend-icon {
  font-size: 0.875rem;
}

.trend-up { color: #10b981; }
.trend-down { color: #ef4444; }
.trend-neutral { color: var(--grey-500); }

.trend-value {
  font-size: 0.875rem;
  font-weight: 600;
}

.trend-value.trend-up { color: #10b981; }
.trend-value.trend-down { color: #ef4444; }
.trend-value.trend-neutral { color: var(--grey-500); }

.stat-content {
  text-align: left;
}

.stat-value {
  font-size: 2rem;
  font-weight: 700;
  margin: 0 0 0.25rem 0;
  color: var(--grey-800);
}

.stat-title {
  font-size: 0.875rem;
  color: var(--grey-600);
  margin: 0 0 0.25rem 0;
  font-weight: 500;
}

.stat-subtitle {
  font-size: 0.75rem;
  color: var(--grey-500);
  margin: 0;
}

/* Chart Card */
.chart-card {
  background: var(--white-color);
  border-radius: var(--border-radius-lg);
  padding: 1.5rem;
  box-shadow: var(--shadow);
  margin-bottom: 1.5rem;
}

.chart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid var(--grey-200);
}

.chart-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--grey-800);
  margin: 0;
}

.chart-actions {
  display: flex;
  gap: 0.5rem;
}

.chart-content {
  min-height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Data Table */
.data-table {
  background: var(--white-color);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow);
  overflow: hidden;
}

.table-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 1px solid var(--grey-200);
}

.table-header h3 {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--grey-800);
  margin: 0;
}

.table-actions {
  display: flex;
  gap: 0.5rem;
}

.table-container {
  overflow-x: auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
}

.table th,
.table td {
  padding: 1rem;
  text-align: left;
  border-bottom: 1px solid var(--grey-200);
}

.table th {
  background: var(--grey-50);
  font-weight: 600;
  color: var(--grey-700);
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.table td {
  color: var(--grey-600);
}

.table tbody tr:hover {
  background: var(--grey-50);
}

.table-loading,
.table-empty {
  padding: 3rem;
  text-align: center;
  color: var(--grey-500);
}

.loading-spinner {
  width: 2rem;
  height: 2rem;
  border: 2px solid var(--grey-300);
  border-top: 2px solid var(--accent-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Quick Action Card */
.quick-action-card {
  background: var(--white-color);
  border-radius: var(--border-radius-lg);
  padding: 1.5rem;
  box-shadow: var(--shadow);
  transition: all 0.3s ease;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 1rem;
  border-left: 4px solid var(--accent-color);
}

.quick-action-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.quick-action-card.disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.quick-action-card.disabled:hover {
  transform: none;
  box-shadow: var(--shadow);
}

.action-primary { border-left-color: var(--accent-color); }
.action-success { border-left-color: #10b981; }
.action-warning { border-left-color: #f59e0b; }
.action-danger { border-left-color: #ef4444; }
.action-info { border-left-color: #3b82f6; }

.action-icon {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: var(--white-color);
  background: var(--accent-color);
}

.action-primary .action-icon { background: var(--accent-color); }
.action-success .action-icon { background: #10b981; }
.action-warning .action-icon { background: #f59e0b; }
.action-danger .action-icon { background: #ef4444; }
.action-info .action-icon { background: #3b82f6; }

.action-content h4 {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--grey-800);
  margin: 0 0 0.25rem 0;
}

.action-content p {
  font-size: 0.875rem;
  color: var(--grey-600);
  margin: 0;
}

/* Progress Bar */
.progress-container {
  margin-bottom: 1rem;
}

.progress-small { margin-bottom: 0.5rem; }
.progress-large { margin-bottom: 1.5rem; }

.progress-label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
  font-size: 0.875rem;
  color: var(--grey-600);
}

.progress-bar {
  width: 100%;
  height: 0.5rem;
  background: var(--grey-200);
  border-radius: 0.25rem;
  overflow: hidden;
}

.progress-small .progress-bar { height: 0.25rem; }
.progress-large .progress-bar { height: 0.75rem; }

.progress-fill {
  height: 100%;
  border-radius: 0.25rem;
  transition: width 0.3s ease;
}

.progress-primary { background: var(--accent-color); }
.progress-success { background: #10b981; }
.progress-warning { background: #f59e0b; }
.progress-danger { background: #ef4444; }
.progress-info { background: #3b82f6; }

/* Metric Card */
.metric-card {
  background: var(--white-color);
  border-radius: var(--border-radius-lg);
  padding: 1.5rem;
  box-shadow: var(--shadow);
  transition: all 0.3s ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.metric-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.metric-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--accent-color);
}

.metric-primary::before { background: var(--accent-color); }
.metric-success::before { background: #10b981; }
.metric-warning::before { background: #f59e0b; }
.metric-danger::before { background: #ef4444; }
.metric-info::before { background: #3b82f6; }
.metric-purple::before { background: #8b5cf6; }

.metric-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.metric-icon {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.25rem;
  color: var(--white-color);
}

.metric-primary .metric-icon { background: var(--accent-color); }
.metric-success .metric-icon { background: #10b981; }
.metric-warning .metric-icon { background: #f59e0b; }
.metric-danger .metric-icon { background: #ef4444; }
.metric-info .metric-icon { background: #3b82f6; }
.metric-purple .metric-icon { background: #8b5cf6; }

.metric-trend {
  font-size: 0.75rem;
  font-weight: 600;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
}

.metric-trend.trend-up {
  background: #dcfce7;
  color: #166534;
}

.metric-trend.trend-down {
  background: #fef2f2;
  color: #991b1b;
}

.metric-content {
  text-align: left;
}

.metric-value {
  font-size: 1.75rem;
  font-weight: 700;
  margin: 0 0 0.25rem 0;
  color: var(--grey-800);
}

.metric-title {
  font-size: 0.875rem;
  color: var(--grey-600);
  margin: 0 0 0.25rem 0;
  font-weight: 500;
}

.metric-subtitle {
  font-size: 0.75rem;
  color: var(--grey-500);
  margin: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .stat-card,
  .chart-card,
  .data-table,
  .quick-action-card,
  .metric-card {
    margin-bottom: 1rem;
  }

  .stat-header,
  .chart-header,
  .table-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }

  .quick-action-card {
    flex-direction: column;
    text-align: center;
  }

  .table-container {
    font-size: 0.875rem;
  }

  .table th,
  .table td {
    padding: 0.75rem 0.5rem;
  }
}

@media (max-width: 480px) {
  .stat-value,
  .metric-value {
    font-size: 1.5rem;
  }

  .stat-icon,
  .metric-icon,
  .action-icon {
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1.25rem;
  }
}
